import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Error from '../../components/error';
import Message from '../../components/message';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	resetEmailMessage = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailMessage', '');
		userStore.updateField('emailMessageIcon', '');
	}

	resetEmailError = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailError', '');
		userStore.updateField('emailErrorIcon', '');
	}

	toggleSetting= async (field) => {
        const { userStore } = this.props.stores;
		const { user } = userStore;
		const { settings = {} } = user;
        await userStore.updateSetting({ [`settings.${field}`]: settings[field] === 1 ? 0 : 1 });
    }

	subscriptionCreate = async () => {
		const { paypalStore, userStore } = this.props.stores;
		this.setState({ paypalLoading: true });
		await paypalStore.subscriptionCreate({});
		await userStore.getInfo();
		this.setState({ paypalLoading: false });
	}

	subscriptionCancel = async (e) => {
		const { id } = e.target.closest('button').dataset;
		const { paypalStore, userStore } = this.props.stores;
		await paypalStore.subscriptionCancel({ planId: id });
		await userStore.getInfo();
	}

	subscriptionDetail = async () => {
		const { paypalStore, userStore } = this.props.stores;
		const { user = {} } = userStore;
		const { subscription } = user;

		if (subscription.payPalSubscriptionId) {
			const response = await paypalStore.subscriptionDetail({ id: subscription.payPalSubscriptionId });
			userStore.setSubscriptionPaypal(response.data);
			await userStore.getInfo();
		}
	}

	toggleCancel = () => {
		const { showCancel } = this.state;
		this.setState({
			showCancel: !showCancel,
		});
	}
	toggleDetails = () => {
		const { showDetails } = this.state;
		if (!showDetails) {
			this.subscriptionDetail();
		}
		this.setState({
			showDetails: !showDetails,
		});
	}

	getPaypalButton() {
		const { paypalLoading, showCancel, showDetails } = this.state;
		const { userStore } = this.props.stores;
		const { user = {}, subscriptionPaypal = {} } = userStore;
		const { subscription = {} } = user;

		if (subscription && subscription.isActive) {
			if (showDetails) {
				const { subscriber = {}, billing_info = {} } = subscriptionPaypal;
				const { name = {} } = subscriber;
				const { last_payment = {} } = billing_info;
				return (<>
					<h5>Premium subscription</h5>
					<table class='table table-sm table-striped'>
						<tbody>
							<tr><th>Email</th><td>{subscriber.email_address}</td></tr>
							<tr><th>Name</th><td>{name.given_name} {name.surname}</td></tr>
							<tr><th>Next billing</th><td>{util.isoDate(billing_info.next_billing_time)}</td></tr>
							<tr><th>Last payment</th><td>
								{util.isoDate(last_payment.time)} {last_payment.amount && <>
									{last_payment.amount.currency_code} {last_payment.amount.value}
								</>}
							
							</td></tr>
						</tbody>
					</table>
					{showCancel ? <>
						<div class='mb-4'>
							Are you sure you want to cancel your subscription?
						</div>
						<button type='button' class='btn btn-success btn-sm' onClick={this.toggleCancel} disabled={paypalLoading}>
							No, I want to continue as a Premium member :)
						</button>
						<button type='button' class='btn btn-danger btn-sm ml-5' onClick={this.subscriptionCancel} disabled={paypalLoading} data-id={subscription.payPalSubscriptionId}>
							Cancel subscription {paypalLoading && <>
								<span class='spinner-border spinner-border-sm' role='status' aria-hidden='true' />
								<span class='sr-only'>Loading...</span>
  							</>}
						</button>
					</> : <>
						<div class='mb-4'>
							<button type='button' class='btn btn-primary btn-block btn-lg' onClick={this.toggleDetails} disabled={paypalLoading}>
								Back to subscription
							</button>
						</div>
						<div class='mt-4 pt-5 text-right'>
							<small class='font-weight-lighter' onClick={this.toggleCancel}>
								Cancel subscription
							</small>
						</div>
					</>}
				</>);
			}
			return (<>
				<h5>Premium subscription</h5>

				<div class='display-1 text-center text-success'>
					<i class='fas fa-user-plus' />
				</div>
				<div class='d-flex'>
					<div class='display-4 text-success float-left'>
						<i class='fas fa-check' />
					</div>
					<div class='align-self-stretch ml-3 pt-3'>
						Active premium subscription since {util.isoDate(subscription.startDate)}<br />
						<small class='font-weight-lighter' onClick={this.toggleDetails}>
							Subscription details 
						</small>
					</div>
				</div>
				<div class='mt-4'>
					<p>
						Thank you so much for beeing a Premium member and supporting this app üòç.
					</p>
					<p>
						If you have any questions, feel free to contact us at Facebook.
					</p>
				</div>
			</>);
		}

		if (subscription && subscription.approveLinkObject) {
			const { href } = subscription.approveLinkObject;
			return (<>
				<h5>Become a Premium member of The Musher app.</h5>

				<div class='container-fluid mt-4'>
					<div class='row'>
						<div class='col-4 px-0'>
							<div class='border h-100 rounded-lg p-3 mr-2 border-success'>
								1. Create subscription <i class='fas fa-check text-success' />
							</div>
						</div>
						<div class='col-4 px-0'>
							<div class='border h-100 rounded-lg p-3 mr-2 font-weight-bold'>
								2. Approve payment
							</div>
						</div>
						<div class='col-4 px-0'>
							<div class='border h-100 rounded-lg p-3 mr-2 text-muted'>
								3. Done
							</div>
						</div>
					</div>
				</div>

				<div class='mt-4'>
					<p>
						You are just one step away from a super-duper Premium subscription üòç.
					</p>
					<p>
						The final step is too approve your subscription at Paypal.com.<br />
						Click this button to open PayPal in a new browser window.
					</p>
				</div>
				<a class='btn btn-primary btn-lg btn-block' href={href} target='_blank' rel='noreferrer'>
					Approve payment at PayPal.com
				</a>
				<button class='btn btn-link btn-sm btn-block' onClick={this.abortSubscriptionApprove}>
					Cancel and try again
				</button>
			</>);
		}

		return (<>
			<div class='mb-4'>
				<h5>Become a Premium member of The Musher app.</h5>
				<div>
					<div class='display-1 text-center text-success'>
						<i class='fas fa-user-plus' />
					</div>
					<p>
						By becoming a Premium member of The Musher app you support the development and operations of this app.
						It's a lot of work maintaining the source code, servers and databases, so we are very grateful for your support.
					</p>
					<p>
						In addition to supporting us you also get access to premium features which is very useful for dog training and care.
					</p>
					<p>
						New features will be added continously and you as a Premium member can be a part of the decisions on which features should be added.
					</p>
					<p>
						Subscription payment is supported via Paypal.com.
					</p>
				</div>

				<h5>Some of the features</h5>
				<table class='table table-sm'>
					<thead>
						<tr>
							<th>Feature</th>
							<th class='text-center'>Basic</th>
							<th class='text-center'>Premium</th>
						</tr>
					</thead>
					<tbody>
						<tr>
							<td>Social interaction</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Multiple team members</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Dog history</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Dog todolist</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Public tracks</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Workout tracking</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Workout integrations (Garmin, Strava)</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Basic workout analysis</td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Advanced workout analysis</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Workout trends</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Workout plans</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Team fitness</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Compare your team with others</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Race planner</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Race ready indicator</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Weather from yr.no</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
						<tr>
							<td>Premium badge inside the app</td>
							<td class='text-center'><i class='fas fa-times text-danger' /></td>
							<td class='text-center'><i class='fas fa-check text-success' /></td>
						</tr>
					</tbody>
				</table>
			</div>


			<div class='container-fluid mt-4'>
				<div class='row'>
					<div class='col-4 px-0'>
						<div class='border h-100 rounded-lg p-3 mr-2 font-weight-bold'>
							1. Create subscription
						</div>
					</div>
					<div class='col-4 px-0'>
						<div class='border h-100 rounded-lg p-3 mr-2 text-muted'>
							2. Approve payment
						</div>
					</div>
					<div class='col-4 px-0'>
						<div class='border h-100 rounded-lg p-3 mr-2 text-muted'>
							3. Done
						</div>
					</div>
				</div>
			</div>

			<button type='button' class='btn btn-success btn-lg btn-block mt-4' onClick={this.subscriptionCreate} disabled={paypalLoading}>
				Subscripe to Premium / Monthly $5 {paypalLoading && <>
					<span class='spinner-border spinner-border-sm' role='status' aria-hidden='true' />
					<span class='sr-only'>Loading...</span>
				</>}
			</button>								
		</>);
	}

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { scrolledDown } = this.props;
		const { userStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const { user, emailMessage, emailError, emailMessageIcon, emailErrorIcon, isAdmin } = userStore;
		const { teams, settings = {} } = user;
		const allTeams = userStore.findTeams(teams);
		const apiServer = util.getApiServer();
		const jwtToken = util.getJwtToken();
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row fixed-top' style={`margin-top: ${scrolledDown > 0 ? 0 : SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'}; transition: all 0.3s ease-in-out;`}>
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users' class='text-secondary stretched-link'><i class='fas fa-user' /> <Text id='users.title'>Din profil</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/settings' class='text-secondary stretched-link'><i class='fas fa-sliders-h' /> <Text id='users.settings'>Innstillinger</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu text-overflow border-bottom border-primary'>
						<nobr><a href='/users/subscription' class='text-primary stretched-link'><i class='fas fa-signature' /> <Text id='users.subscriptions'>Abonnement</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/integrations' class='text-secondary stretched-link'><i class='fas fa-plug' /> <Text id='users.integrations'>Integrasjoner</Text></a></nobr>
					</div>
				</div>

				{isAdmin && <div class='row border-top mt-5'>
					<div class='col-12'>
						{this.getPaypalButton()}
					</div>
				</div>}

			</div>
		);
	}
}

export default Profile;
